import React, { Component } from 'react';
import './App.css';
import Dial from './Dial';
import _ from 'lodash';
import playlist from './playlist.json';
import { fadeInOut } from './AudioHelper';
import * as Vibrant from 'node-vibrant';

let PLAYLIST = _.filter(playlist.items, (item)=> { return !!item.track.preview_url });


class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      progress: 0,
      duration: 1000,
      trackIndex: Math.floor(Math.random()*PLAYLIST.length),
      background: 'lightblue',
      foreground: 'black',
      accent: 'darkblue'
    }
    //this.audio = new Audio('https://p.scdn.co/mp3-preview/4df2db188e91a78bf51bba78a1862af53f127416?cid=6af9fee1b9d9481d9a25de3272b1b557');
    this.updateProgress = this.updateProgress.bind(this);
    this.updateBackground = this.updateBackground.bind(this);
    window.setInterval(this.updateProgress, 1000);
  }

  componentDidMount() {
    this.changeTrack(this.state.trackIndex)
  }

  componentDidUpdate(prevProps, prevState) {
    if(prevState.trackIndex !== this.state.trackIndex) {
      this.changeTrack(this.state.trackIndex);
    }
  }

  changeTrack(index) {
    if(this.audio) this.audio.pause();
    this.audio = new Audio(PLAYLIST[index].track.preview_url);
    this.audio.currentTime = 0;
    fadeInOut(this.audio);
    this.audio.play();
    Vibrant.from(_.get(PLAYLIST[index], 'track.album.images[0].url'))
    .getPalette().then(this.updateBackground);
  }

  updateProgress() {
    if(this.audio.currentTime !== this.audio.duration) {
      this.setState({progress: (this.audio.currentTime/this.audio.duration*200)%200});
    } else {
      let nextIndex = (this.state.trackIndex + 1)%PLAYLIST.length;
      this.setState({ trackIndex: nextIndex });
    }
  }

  updateBackground(palette) {
    console.log(palette);
    let { DarkVibrant, LightVibrant, DarkMuted, Vibrant, Muted, LightMuted } = palette;

    let background = DarkVibrant || DarkMuted;
    let foreground = LightVibrant || LightMuted;
    let accent = Vibrant || Muted || LightMuted;

    this.setState({
      background: `rgb(${background._rgb[0]}, ${background._rgb[1]}, ${background._rgb[2]})`,
      foreground: `rgb(${foreground._rgb[0]}, ${foreground._rgb[1]}, ${foreground._rgb[2]})`,
      accent: `rgb(${accent._rgb[0]}, ${accent._rgb[1]}, ${accent._rgb[2]})`
    });
  }

  getProgress() {
    if(!this.audio) return 0;
    return (this.audio.currentTime/this.audio.duration*200)%200;
  }

  render() {
    let {accent, foreground, background, trackIndex} = this.state;

    let progress = this.getProgress();

    let prevTrackIndex = trackIndex ? trackIndex - 1 : PLAYLIST.length -1;
    let nextTrackIndex = trackIndex === PLAYLIST.length - 1 ? 0 : trackIndex + 1;

    let track = PLAYLIST[trackIndex];
    let prevTrack = PLAYLIST[prevTrackIndex];
    let nextTrack = PLAYLIST[nextTrackIndex];
    return (
      <div className="App" style={{ transition: 'color 1s, background-color 1s', color: foreground, backgroundColor: background, display: 'flex', flexDirection: 'row', justifyContent: 'center' }}>
        <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', height: 500, width: 500, padding: 20, margin: 'auto', borderRadius: 20 }}>
          <div style={{ height: 300}}>
            <Dial color={accent} progress={progress}/>
          </div>
          <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center' }}>
            <div onClick={()=>this.setState({trackIndex: prevTrackIndex})} style={{ cursor: 'pointer', height: 100, width: 100, margin: 'auto', marginTop: -100, borderRadius: 50, backgroundImage: `url(${ _.get(prevTrack, 'track.album.images[0].url') })`, backgroundPosition: 'center', backgroundSize: 'cover' }}>
            </div>
            <div style={{ height: 220, width: 220, margin: 'auto', marginTop: -260, borderRadius: 110, backgroundImage: `url(${ _.get(track, 'track.album.images[0].url') })`, backgroundPosition: 'center', backgroundSize: 'cover' }}>
            </div>
            <div onClick={()=>this.setState({trackIndex: nextTrackIndex})} style={{ cursor: 'pointer', height: 100, width: 100, margin: 'auto', marginTop: -100, borderRadius: 50, backgroundImage: `url(${ _.get(nextTrack, 'track.album.images[0].url') })`, backgroundPosition: 'center', backgroundSize: 'cover' }}>
            </div>
          </div>
          <br/>
          <br/>
          <h1>{_.get(track, 'track.name')}</h1>
          <h3>{_.get(track, 'track.artists[0].name')}</h3>
        </div>
      </div>
    );
  }
}

export default App;



